.ecotope.contain {
  padding: .9rem 0;
  display: flex;
}
.ecotope.contain .cont-box {
  width: 25vw;
}
.ecotope.contain .cont-left {
  margin-left: 10px;
  flex: 3;
}
.ecotope.contain .cont-right {
  margin-right: 10px;
  flex: 3;
}
.ecotope.contain .cont-center {
  flex: 6;
}
.ecotope.contain .cont-center .cont-list ul li {
  width: 220px;
  height: 130px;
  color: #4df1bd;
}
.ecotope.contain .cont-center .cont-list ul li:last-child {
  color: #ffc90d;
}
